/* "Cera Pro" */

:root{
  --background-white: #F7F9FF;

  --accent-oragne: #FFA400;
  --accent-dark: #323286;
  --accent-light: #EAF6FF;

  --black-light: #37393C;
  --black-dark: #232528;

  --gray-light: #C8CCD0;

  /* https://coolors.co/eaf6ff-f7f9ff-ffa400-323286-37393c-232528-c8ccd0  */
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 
  'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  background-color: var(white);

  
}

